.widgetSm {
  flex: 1;
  margin-right: 20px;
  padding: 20px;
  -webkit-box-shadow: 2px 2px 31px -13px rgba(106, 156, 194, 0.85);
  box-shadow: 2px 2px 31px -13px rgba(173, 206, 231, 0.85);
}


.widgetSmUser{
    display: flex;
    flex-direction: column;
}
.widgetSmTitle{
    font-size: 22px;
    font-weight: 600;
    
    }
.widgetSmImg{
    height: 40px;
    width: 40px;

    object-fit: cover;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}

.widgetSmList{
    /* background-color: green; */
    padding: 0;
    margin: 0;
    list-style: none;
}

.widgetSmItem{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 20px 0px;
    
}


.widgetSmUsername{
    font-weight: 600;
}
.widgetSmUserTitle{
    font-weight: 300;
}


.widgetSmButton{
    display: flex;
    align-items: center;
    border: none;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    padding:7px  10px;
    background-color: #eeeef7;
    color: #555;
    cursor: pointer;
}


.widgetSmIcon{
    margin-right: 5px;
    font-size: 16px;
}

















