.newUser{
    flex: 4;
    margin: 40px;
}
.newUserForm{
    display: flex;
    flex-wrap: wrap;
}


.newUserItem{
    width: 500px;
    display: flex;
    flex-direction: column;
    margin-top: 10px;
    margin-right: 20px;
}

.newUserItem >label{
  margin-bottom: 10px;
  font-size: 14px;
  font-weight: 600;
  color: #444;
}

.newUserItem >input{
  height: 20px;
  padding: 10px;
  border: 1px solid rgb(192, 190, 190);
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}
  

.newUserGender >input{
    margin-top: 15px;
}



.newUserGender >label{
    margin: 10px;
    font-size: 15px;
    color: gray;
}



.newUserSelect{
    height: 40px;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    color: gray;
}

.newUserButton{
    width: 200px;
    border: none;
    background-color: rgb(87, 87, 163);
    color: white;
    padding: 7px 10px;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    margin-top: 30px;
    cursor: pointer;
}



























