.featured{
    width: 100%;
    margin-top: 30px;
    /* background-color: red; */
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.featuredItem{
    flex: 1;
    /* background-color: green; */
    margin: 0px 20px;
    padding: 30px;
    border-radius: 10px;
    cursor:pointer;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    -webkit-box-shadow: 2px 2px 31px -13px rgba(106, 156, 194, 0.85); 
    box-shadow: 2px 2px 31px -13px rgba(173, 206, 231, 0.85);
}


.featuredTitle{
    font-size: 20px;
}
.featuredMoneyContainer{
    margin: 10px 0px;
    display: flex;
    align-items: center;
}

.featuredMoney{
    font-size: 30px;
    font-weight: 600;
}


.featuredMoneyRate{
    display: flex;
    align-items: center;
    margin-left: 12px;
}

.featuredIcon{
    font-size: 14px;
    margin-left: 5px;
    color: green;
}
.negative{
color: red;
}

.featuredSub{
    font-size: 15px;
    color: gray;
}






