.product {
  flex: 4;
  padding: 20px;
}

.productAddButton {
  cursor: pointer;
  width: 80px;
  border: none;
  padding: 5px;
  color: white;
  font-style: 16px;
  background-color: teal;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}

.productTop {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.productTitleContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.productTopLeft,
.productTopRight {
  flex: 1;
}
.productTopLeft {
}
.productTopRight {
  padding: 50px;
  height: 50%;
  -webkit-box-shadow: 2px 2px 31px -13px rgba(106, 156, 194, 0.85);
  box-shadow: 2px 2px 31px -13px rgba(173, 206, 231, 0.85);
}
.productInfoImg {
  height: 40px;
  width: 40px;
  margin-right: 20px;
  object-fit: cover;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
}

.productInfoTop {
  display: flex;
  align-items: center;
}

.productName {
  font-weight: 600;
}

.productInfoKey {
  font-weight: 600;
}

.productButtom {
  margin-top: 20px;
}
.productInfoBottom {
  margin-top: 20px;
}

.productInfoItems {
  margin-top: 20px;
}
.productInfoItem {
  width: 150px;
  display: flex;
  justify-content: space-between;
}

.productInfoValue {
  font-weight: 300;
}

.productBottom {
  padding: 50px;
  margin: 50px;
  -webkit-box-shadow: 2px 2px 31px -13px rgba(106, 156, 194, 0.85);
  box-shadow: 2px 2px 31px -13px rgba(173, 206, 231, 0.85);
}

/* LOWER COMPONENT */

.productForm {
  display: flex;
  justify-content: space-between;
}

.productFormLeft {
  display: flex;
  flex-direction: column;
}

.productFormLeft >label {
  margin-bottom: 10px;
  color: gray;
  }
  .productFormLeft >input {
    margin-bottom: 10px;
    color: gray;
    border: none;
    padding: 5px;
    border-bottom: 1px solid gray;
    }
  
    .productFormLeft >select {
        margin-bottom: 10px;
        color: gray;
        border: none;
        padding: 5px;
        border-bottom: 1px solid gray;
        }
      
.productFormImg {
  height: 100px;
  width: 100px;
  border-radius: 20px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -ms-border-radius: 20px;
  -o-border-radius: 20px;
  object-fit: cover;
  margin-right: 20px;
}

.productFormRight{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.productUpload{
    display: flex;
    align-items: center;
}


.productButton{
    border: none;
    color: white;
    padding: 5px 10px;
    cursor: pointer;
    font-weight: bold;
    background-color: darkblue;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}












