.user {
  flex: 4;
  padding: 20px;
}
.userTitleContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.userAddButton {
  cursor: pointer;
  width: 80px;
  border: none;
  padding: 5px;
  color: white;
  font-style: 16px;
  background-color: teal;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}

.userContainer {
  display: flex;
}

.userShow {
  flex: 1;
  margin-right: 20px;
  padding: 20px;
  -webkit-box-shadow: 2px 2px 31px -13px rgba(106, 156, 194, 0.85);
  box-shadow: 2px 2px 31px -13px rgba(173, 206, 231, 0.85);
}
.userUpdate {
  flex: 2;
  padding: 20px;
  -webkit-box-shadow: 2px 2px 31px -13px rgba(106, 156, 194, 0.85);
  box-shadow: 2px 2px 31px -13px rgba(173, 206, 231, 0.85);
}

.userShowTop {
  display: flex;
  align-items: center;
}

.userShowImg {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  margin-right: 20px;
}
.userShowTopTitle {
  display: flex;
  flex-direction: column;
}

.userShowButtom {
  margin-top: 20px;
}
.userShowUsername {
  font-weight: 600;
}
.userShowUserTitle {
  font-weight: 300;
  font-size: 16px;
  color: rgb(134, 133, 133);
}

.userShowTitle {
 
  font-weight: 600;
  font-size: 18px;
  color: rgb(134, 133, 133);
 
 
}
.userShowInfoTitle {
  font-size: 14px;
}
.userShowInfo {
  display: flex;
  align-items: center;
  margin-top: 20px;
  color: rgb(134, 133, 133);
}

.userShowIcon {
  color: rgb(134, 133, 133);
  margin-right: 20px;
}

.userUpdateImg{
    width: 100px;
    height: 100px;
    border-radius:20% ;
    -webkit-border-radius:20% ;
    -moz-border-radius:20% ;
    -ms-border-radius:20% ;
    -o-border-radius:20% ;
    object-fit: cover;
    margin-right: 20px;
}
.userUpdateForm{
    display: flex;
    justify-content: space-between ;
    margin-top: 20px;
}
.userUpdateItem{
    display: flex;
    flex-direction: column;
    margin-top: 10px;
}
.userUpdateItem >label{
   margin-bottom: 5px;
   font-size: 14px;
}
.userUpdateInput{
    border: none;
    width: 250px;
    border-bottom: 1px solid gray;
    height: 30px;
}
.userUpdateTitle{
    font-size: 24px;
    font-weight: 600;
}

/* RIGHT SIDE */
.userUpdateRight{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.userUpdateUpload{
    display: flex;
    align-items: center;
}
.userUpdateIcon{
    cursor: pointer;
}

.userUpdateButton{
    border: none;
    cursor: pointer;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    color: white;
    background-color: rgb(124, 124, 236);
    padding: 5px;
    font-weight: bold;
}











































