.widgetLg{
    flex: 2;
    
    padding: 20px;
    -webkit-box-shadow: 2px 2px 31px -13px rgba(106, 156, 194, 0.85);
    box-shadow: 2px 2px 31px -13px rgba(173, 206, 231, 0.85);
}
.widgetLgTitle{
    font-size: 22px;
    font-weight: 600;
   
}

.widgetLgImg{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;

}

.widgetLgButton{
    padding: 5px 7px;
    border: none;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
}


.widgetLgButton.Approved{
    background-color: rgb(235, 255, 235);
    color: rgb(61, 105, 61);
}


.widgetLgButton.Declined{
    background-color: rgb(250, 218, 234);
    color: rgb(190, 70, 70);
}

.widgetLgButton.Pending{
    background-color: rgb(219, 223, 245);
    color: rgb(48, 60, 228);
}

.widgetLgtable{
   width: 100%;
}


.widgetLgTh{
    text-align: left;
}

.WidgetLgUser{
    display: flex;
    align-items: center;
    font-weight: 600;
}

.widgetLgDate, .widgetLgAmount{
    font-weight: 300;
}




























