.newProduct {
  flex: 4;
  margin: 30px;
}

.addProductItem {
}

.addProductForm {
  margin-top: 10px;
}

.addProductItem {
  width: 250px;
  /* background-color: red; */
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
}

.addProductItem > label {
  color: gray;
  font-weight: 600;
  margin-bottom: 10px;
}

.addProductItem > input {
  padding: 10px;
}

.addProductItem > select {
  padding: 10px;
}

.addProductButton {
  margin-top: 10px;
  padding: 7px 10px;
  border: none;
  color: white;
  font-weight: bold;
  background-color: rgb(65, 65, 170);
  cursor: pointer;
  border-radius:5px;
  -webkit-border-radius:5px;
  -moz-border-radius:5px;
  -ms-border-radius:5px;
  -o-border-radius:5px;
}
