.userList {
  flex: 4;
  margin: 30px;
}

.userListUser {
  display: flex;
  align-items: center;
}

.userListImg {
    height: 32px;
    width: 32px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}

.userListEdit{
    border: none;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    padding: 5px 15px;
    color: white;
    cursor: pointer;
    background-color: rgb(34, 197, 61);
    margin-right: 20px;
}
.userListDelete{
    color: rgb(247, 185, 185);
    cursor: pointer;
}










































