.sidebar {
  flex: 1;
  height: calc(100vh - 50px);
  background-color: rgb(251,251,255);
  /* background-color: rgb(74, 74, 204); */
  position: sticky;
  top: 50px;
}
.sidebarWrapper{
    padding: 20px;
    color: #555;
    /* background-color: rgb(88, 143, 88); */
}
.sidebarMenu{
    /* background-color: rgb(189, 107, 107); */
    margin-bottom: 10px;
}

.sidebarTitle{
    font-size: 13px;
    color: rgb(187,186,186);
    
}

.sidebarList{
    list-style: none;
    padding: 5px;
}

.sidebarListItems{
    padding: 5px;
    cursor: pointer;
    display: flex;
    align-items: center;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
}

.sidebarListItems.active, .sidebarListItems:hover{
    background-color: rgb(228,228,250);
}

.sidebarIcon{
    margin-right: 5px;
    font-style: 20px!important;
}











