    flex: 4;
}

.productList {
    flex: 4;
    margin: 30px;
  }
  
  .productListUser {
    display: flex;
    align-items: center;
  }
  
  .productListImg {
      height: 32px;
      width: 32px;
      border-radius: 50%;
      -webkit-border-radius: 50%;
      -moz-border-radius: 50%;
      -ms-border-radius: 50%;
      -o-border-radius: 50%;
      object-fit: cover;
      margin-right: 10px;
  }
  
  .productListEdit{
      border: none;
      border-radius: 5px;
      -webkit-border-radius: 5px;
      -moz-border-radius: 5px;
      -ms-border-radius: 5px;
      -o-border-radius: 5px;
      padding: 5px 15px;
      color: white;
      cursor: pointer;
      background-color: rgb(34, 197, 61);
      margin-right: 20px;
  }
  .productListDelete{
      color: rgb(247, 185, 185);
      cursor: pointer;
  }
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  



































































